import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import {init,animate,setColor} from './Three/script.js'

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      color: "yellow"
    };
    this.selectColor = this.selectColor.bind(this);
  }
  selectColor(event) {
    var newColor = event.target.className.split(' ')[1]
    if (!newColor) return
    console.log(newColor)
    this.setState({
      color: newColor
    });
    setColor(newColor)
    if(newColor) {
      
      document.querySelector('.paletteContainer>.selected').classList.remove("selected")
      event.target.classList.add("selected");
    }

  }
	componentDidMount() {
		init();
		animate();
	}
  render() {
    return (
      <div className="App">
        <div className="Toolbar">
          <div className="logo">
            <img className='logoimg' src={process.env.PUBLIC_URL + '/lego_name.png'}/>
          </div>
        </div>
        <div className="Store">
          <p>Color</p>
          <div className="paletteContainer">
            <div className="palette yellow selected" onClick={this.selectColor}></div>
            <div className="palette red" onClick={this.selectColor}></div>
            <div className="palette orange" onClick={this.selectColor}></div>
            <div className="palette green" onClick={this.selectColor}></div>
            <div className="palette blue" onClick={this.selectColor}></div>
          </div>

        </div>
          
        <div className="World"></div>
      </div>
    );
  }
}

export default App;